export default {
	name: 'mmt-input-number-config',
	props: {
		config: Object,
		info: Object
	},
	methods: {
		calcPrecision(e) {
			let reg = /^\d+(\.\d+)?$/;
			if (!reg.test(e)) {
				this.$Message.error("输入错误！");
				return;
			}
			let a = e.toString().split(".");
			if (a.length > 1) {
				this.config.precision = a[1].length;   // 设置精度
			} else {
				this.config.precision = 0;
			}
		}
	},
	computed: {}
};
