export default {
	name: 'mmt-date-picker-config',
	props: {
		config: Object,
		info: Object
	},
	methods: {
		changeFormat(e) {
			if (e === 'yyyy-MM-dd HH:mm'){
				this.config.type = 'datetime';
			} else {
				this.config.type = 'date';
			}
		}
	},
	computed: {}
};
